import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ViewController,LoadingController,ModalController,IonicApp } from 'ionic-angular';
import { CommonProvider } from '../../../providers/common';
import { CusupdatePage } from '../../cusupdate/cusupdate';
import { AddQuickCus } from '../add-quick-cus/add-quick-cus';

/**
 * Generated class for the CusSearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-customer',
  templateUrl: 'customer.html',
  providers: [CommonProvider]

})
export class CusSearchPage {

  customers:any[] = [];
  toggle: any = true;

  constructor(public ionicApp: IonicApp,public modal:ModalController,public load:LoadingController, public common:CommonProvider,public viewCtrl: ViewController,public navCtrl: NavController, public navParams: NavParams) {

  }

  selected(data){
    this.viewCtrl.dismiss(data);
  }

  getCus(ev:any){
    const val = ev.target.value;
    if (val && val.trim() != '' && val.length >=3) {
      // let loader = this.load.create({
      //   content: 'Please Wait',
      //   spinner: 'bubbles',
      // });
      // loader.present();

      this.common.getCustBySearch({"searchTxt":val}).then(data=>{
        this.customers = data;
        // loader.dismiss();
      })
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CusSearchPage');
  }

  dismiss(){
    this.viewCtrl.dismiss();
  }
  openQuickCus(){
    let modal = this.modal.create(AddQuickCus)
    modal.present();
    modal.onDidDismiss(data => {
      console.log(data)
      if(data != null && data != undefined){
        this.viewCtrl.dismiss(data);
      }
    });
  }
  edit(data){

    this.navCtrl.push(CusupdatePage,{'id':data})
  }
}



