    // let fixed_rate_type = parseFloat(this.stoneSelected['fixed_rate_type']);
    // Rate Type => 1-Fixed, 2 - Rate From Master, 3 - Manual
    // if(rate_type == 1 || rate_type == 3)	
    // {
    //   if(fixed_rate_type == 1) //Fixed Rate Type => 1-Per Gram,2-Per Piece
