import { Component } from '@angular/core';
import { IonicPage, NavParams,ViewController,LoadingController } from 'ionic-angular';
import { CommonProvider } from '../../providers/common';
import { RetailProvider } from '../../providers/retail';
import { filter } from 'rxjs/operators';

/**
 * Generated class for the AddStoneDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-other',
  templateUrl: 'other.html',
  providers: [CommonProvider,RetailProvider]

})
export class OtherPage {

  stoneData:any = [{"metal_id":0, "stone_pcs":0, "stone_wt" : 0,"price":0}];
  stoneSelected = {"rate_type":"1"};
  fixed_rate_type:any = "";
  action:any;
  page:any;
  stoneMasData =[];
  stones = [];
  uom = [];
  cut = [];
  clarity = [];
  color = [];
  shape = [];
  errorMsg:any;
  stnErrMsg:any;
  proceedSave = false;

  constructor(public load:LoadingController, public retail:RetailProvider, public common:CommonProvider,public viewCtrl: ViewController, public navParams: NavParams) {
    this.page = this.navParams.get('page');
    this.stoneMasData = this.navParams.get('stoneMasData');
    console.log(this.navParams.get('stone_details'))
    // this.common.othermetal().then(data=>{
    //   this.stones = data;
    // })
    this.stones = [{'metal_id':1,'metal_name':'ring','price':100},{'metal_id':2,'metal_name':'bangle','price':200}]
    if(this.navParams.get('stone_details').length > 0){

      this.stoneData = this.navParams.get('stone_details');
    }
    this.action = this.navParams.get('action');
  }

  dismiss(){
    this.viewCtrl.dismiss();
  }

  updateStone(submitType){  
    this.viewCtrl.dismiss(this.stoneData);
    // Alert Message Timeout
    setTimeout(()=>{
          this.errorMsg = "";
    },this.common.msgTimeout);
  }

  addStone(submitType){
    console.log(this.stoneData)
    this.viewCtrl.dismiss(this.stoneData);
    // Alert Message Timeout
    setTimeout(()=>{
          this.errorMsg = "";
    },this.common.msgTimeout);
  }

  stoneChanged(){
  }

  calcStoneValue(){
  }
add(){
  this.stoneData.push({"metal_id":0, "stone_pcs":0, "stone_wt" : 0,"price":0});

}
remove(i){

  if(this.stoneData.length > 1){
  this.stoneData.splice(i,1);
  }
  else if(this.stoneData.length == 1){
    this.stoneData.splice(i,1);

    this.stoneData.push({"metal_id":0, "stone_pcs":0, "stone_wt" : 0,"price":0});

  }
}
calp(i,j){
  console.log(i);
  console.log(j);
  this.stoneData[i]['price'] = this.stones.filter(data=>data['metal_id'] == j)[0]['price'];

}
}



