import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController } from 'ionic-angular';
import { RetailProvider } from '../../providers/retail';
import { DesignDetailPage } from '../design-detail/design-detail';
import { CommonProvider } from '../../providers/common';

// @IonicPage()
@Component({
  selector: 'page-subdesign',
  templateUrl: 'subdesign.html',
  providers: [RetailProvider,CommonProvider]
})
export class SubdesignPage {

  id_product = this.navParams.get('id_product');
  design = this.navParams.get('design_no');

  empData = JSON.parse(localStorage.getItem('empDetail'));
  designs: any={};
  last_id: any = 0;
  id_ret_category = this.navParams.get('id_category');

  constructor(public comman:CommonProvider,public navCtrl: NavController,public load: LoadingController, public navParams: NavParams , public retailService: RetailProvider) {
  
    let loader = this.load.create({
      content: 'Please Wait',
      spinner: 'bubbles',
    });
    loader.present();

    var postData = {
      'id_employee': this.empData['uid'] != null ? this.empData['uid'] : null,
      'type': 'active',
      'id_product': this.id_product,
      'last_id': this.last_id,
      'design_no':this.design
    }
    this.comman.getsubdesigns(postData).then(data => {

      this.designs = data;
      console.log(this.designs);
      loader.dismiss();

    })

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DesignsPage');
  }

  doInfinite(infiniteScroll: any) {
    console.log(this.designs)
    this.last_id = this.designs[this.designs.length - 1]['id_sub_design'];
    console.log(this.last_id);
    var postData = {
      'id_employee': this.empData['uid'] != null ? this.empData['uid'] : null,
      'type': 'active',
      'id_product': this.id_product,
      'last_id': this.last_id,
      'design_no':this.design

    }
    this.comman.getsubdesigns(postData).then(data => {
      console.log(data)
      for (let index = 0; index < data.length; index++) {
        this.designs.push(data[index])
      }
      infiniteScroll.complete();
    })
  }

  openDesign(design_no){ 
    this.navCtrl.push(DesignDetailPage,{ 'id_category': this.id_ret_category,'id_product': this.id_product,'design_no':this.design,'id_sub_design':design_no,'type':'edit'})  
  }

}



